import React  from 'react';

class TestView extends React.Component {

    constructor(props) {
        super(props);
    
        this.state = {
          
        };
      
        this.handleClick=this.handleClick.bind(this);
        this.handleSubmitClick=this.handleSubmitClick.bind(this);
        this.handleMsg=this.handleMsg.bind(this);

    }
    
    handleClick=()=>{
        this.props.handleTopicSelected("grp0J5IIvmaIs0");
        this.props.sendMessage("123456789");
        
    }

    handleSubmitClick=()=>{
        const topic = this.props.tinode.getTopic("grp0J5IIvmaIs0");
        //const message = this.state.message.trim();

        if (topic) {
            topic.onData = this.handleMsg;
            topic.onInfo = this.handleMsg;
        }
    }

    handleMsg=(data)=>{
        console.log(data);
    }
    render() {
        return (
            <div className="dialog-buttons">
            <button type="submit" className="primary" onClick={this.handleClick}>
                test button
            </button>

            <button type="submit" className="primary" onClick={this.handleSubmitClick}>
                accept button
            </button>
            </div>
        );
    }
}

export default TestView;
